import {
  Attachments,
  Bubble,
  BubbleProps,
  Conversations,
  Prompts,
  Sender,
  Welcome,
  useXAgent,
  useXChat,
} from "@ant-design/x";
import { createStyles } from "antd-style";
import React, {
  useEffect,
  useMemo,
  useState,
  useRef,
  useCallback,
} from "react";

import {
  CloudUploadOutlined,
  CommentOutlined,
  EllipsisOutlined,
  FireOutlined,
  HeartOutlined,
  PaperClipOutlined,
  PlusOutlined,
  ReadOutlined,
  ShareAltOutlined,
  SmileOutlined,
  UserOutlined,
  LoadingOutlined,
  ClockCircleOutlined,
  CodeOutlined,
  LineChartOutlined,
} from "@ant-design/icons";
import {
  Affix,
  Badge,
  Button,
  type GetProp,
  Space,
  Spin,
  Table,
  Typography,
  message,
  Progress,
} from "antd";
import markdownit from "markdown-it";

import useChat from "@/hooks/useChat";
import { logger } from "@/utils/logger";
import { useIdleCallback } from "@/hooks/useIdleCallback";

const log = logger.extend("copilot:playground");
// const log = logger;

/**
 * 
 * AI
 */
interface ThinkingStep {
  id: string;
  content: string;
  timestamp: number;
}

/**
 * 
 * AI
 */
type ProcessingStage = "sending" | "thinking" | "generating" | "complete";

/**
 *  - AI
 * AI
 */
const ThinkingChain: React.FC<{
  steps: ThinkingStep[];
  isVisible: boolean;
  stage: ProcessingStage;
}> = ({ steps, isVisible, stage }) => {
  if (!isVisible) return null;

  // 
  const getStageInfo = () => {
    switch (stage) {
      case "sending":
        return { text: "...", icon: <LoadingOutlined /> };
      case "thinking":
        return { text: "...", icon: <ClockCircleOutlined /> };
      case "generating":
        return { text: "...", icon: <CodeOutlined /> };
      default:
        return { text: "...", icon: <LoadingOutlined /> };
    }
  };

  const stageInfo = getStageInfo();

  return (
    <div className="cs-bg-blue-50 cs-rounded-lg cs-p-4 cs-mb-4 cs-border cs-border-blue-100 cs-animate-fadeIn cs-mt-2">
      <div className="cs-flex cs-items-center cs-mb-3 cs-text-blue-600">
        <Spin size="small" indicator={stageInfo.icon} className="cs-mr-2" />
        <span className="cs-font-medium">{stageInfo.text}</span>
      </div>

      {steps.length > 0 && (
        <div className="cs-space-y-2 cs-pl-6">
          {steps.map((step, index) => (
            <div
              key={step.id}
              className="cs-flex cs-items-start cs-animate-slideIn"
              style={{ animationDelay: `${index * 0.2}s` }}
            >
              <div className="cs-bg-blue-100 cs-text-blue-700 cs-rounded-full cs-w-5 cs-h-5 cs-flex cs-items-center cs-justify-center cs-text-xs cs-mr-2 cs-mt-0.5">
                {index + 1}
              </div>
              <p className="cs-text-gray-700 cs-text-sm">{step.content}</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

/**
 * 
 * 
 */
const ProcessingProgress: React.FC<{ stage: ProcessingStage }> = ({
  stage,
}) => {
  // 
  const getProgressPercent = () => {
    switch (stage) {
      case "sending":
        return 25;
      case "thinking":
        return 50;
      case "generating":
        return 75;
      case "complete":
        return 100;
      default:
        return 0;
    }
  };

  if (stage === "complete") return null;

  return (
    <div className="mb-4">
      <Progress
        percent={getProgressPercent()}
        strokeColor="#1677ff"
        showInfo={false}
        className="animate-pulse"
      />
    </div>
  );
};

/**
 * 
 * antd-style
 */
const useStyle = createStyles(({ token, css }) => {
  return {
    layout: css`
      width: 100%;
      min-height: 100%;
      height: 100%;
      border-radius: ${token.borderRadius}px;
      display: flex;
      flex-direction: column;
      background: ${token.colorBgContainer};
      font-family: AlibabaPuHuiTi, ${token.fontFamily}, sans-serif;

      .ant-prompts {
        color: ${token.colorText};
      }
    `,
    menu: css`
      background: ${token.colorBgLayout}80;
      width: 280px;
      height: 100%;
      display: flex;
      flex-direction: column;
    `,
    conversations: css`
      padding: 0 12px;
      flex: 1;
      overflow-y: auto;
    `,
    chat: css`
      height: 100%;
      width: 100%;
      overflow-y: auto;
      margin: 0 auto;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      padding: ${token.paddingLG}px;
      gap: 16px;
      padding-bottom: 100px;
      scrollbar-width: 'thin',
      scrollbar-gutter: 'stable',
    `,
    messages: css`
      flex: 1;

      p {
       margin-bottom: 0;
      }
    `,
    placeholder: css`
      padding-top: 32px;
    `,
    sender: css`
      box-shadow: ${token.boxShadow};
    `,
    addBtn: css`
      background: #1677ff0f;
      border: 1px solid #1677ff34;
      width: calc(100% - 24px);
      margin: 0 12px 24px 12px;
    `,
    // 
    fadeIn: css`
      animation: fadeIn 0.3s ease-in-out;
      @keyframes fadeIn {
        from {
          opacity: 0;
          transform: translateY(-10px);
        }
        to {
          opacity: 1;
          transform: translateY(0);
        }
      }
    `,
    slideIn: css`
      animation: slideIn 0.3s ease-out;
      @keyframes slideIn {
        from {
          opacity: 0;
          transform: translateX(-10px);
        }
        to {
          opacity: 1;
          transform: translateX(0);
        }
      }
    `,
  };
});

/**
 * Chat BI 
 * 
 */
const Playground: React.FC = () => {
  // Hook
  const {
    loading,
    onPromptsItemClick,
    onSubmit,
    clearMessages,
    roles,
    prompt,
    setPrompt,
    bubbleItems,

    isGeneratingThinking,
    setIsGeneratingThinking,
    processingStage,
    setProcessingStage,
    thinkingChain, setThinkingChain,
    mockThinkingStep
  } = useChat();

  // 
  const prompts = [
    " 7 ",
    " 30 ",
    " 10 ",
    " 1 ",
  ];

  // 
  const { styles } = useStyle();

  // 
  const senderPromptsItems = useMemo(
    () =>
      prompts.map((description, index) => ({
        key: `${index}`,
        description,
        icon: <FireOutlined style={{ color: "#FF4D4F" }} />,
      })),
    [prompts]
  );

  // ====================  ====================
  const [headerOpen, setHeaderOpen] = React.useState(false);
  const [attachedFiles, setAttachedFiles] = React.useState<
    GetProp<typeof Attachments, "items">
  >([]);


  // 
  const [container, setContainer] = React.useState<HTMLDivElement | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  /**
   * 
   * 
   */
  const handleFileChange: GetProp<typeof Attachments, "onChange"> = (info) => {
    setAttachedFiles(info.fileList);
    // 
    if (info.fileList.length > 0 && info.file.status === "done") {
      message.success(` ${info.file.name} `);
    }
  };

  // 
  const attachmentsNode = (
    <Badge dot={attachedFiles.length > 0 && !headerOpen}>
      <Button
        type="text"
        icon={<PaperClipOutlined />}
        onClick={() => setHeaderOpen(!headerOpen)}
        title=""
      />
    </Badge>
  );

  // 
  const senderHeader = (
    <Sender.Header
      title=""
      open={headerOpen}
      onOpenChange={setHeaderOpen}
      styles={{
        content: {
          padding: 0,
        },
      }}
    >
      <Attachments
        beforeUpload={() => false}
        items={attachedFiles}
        onChange={handleFileChange}
        placeholder={(type) =>
          type === "drop"
            ? { title: "" }
            : {
                icon: <CloudUploadOutlined />,
                title: "",
                description: "",
              }
        }
      />
    </Sender.Header>
  );

  /**
   * 
   * 
   */
  const handleSubmit = async (value: string) => {
    if (!value.trim()) return;
    try {
      // 1. 
      // 2. 
      mockThinkingStep();

      // 3. 
      await onSubmit(value);

      // 
      if (attachedFiles.length > 0) {
        setAttachedFiles([]);
      }
    } catch (error) {
      log.error(":", error);
      message.error("");
      setProcessingStage("complete");
      setIsGeneratingThinking(false);
    }
  };

  /**
   * 
   */
  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, []);

  // 
  useEffect(() => {
    scrollToBottom();
  }, [bubbleItems, thinkingChain, scrollToBottom]);

  // 
  useIdleCallback(() => {
    if (container) {
      const scroll = container.querySelector("#chatbi-messages");
      if (scroll) {
        scroll.scrollTo({
          top: scroll.scrollHeight,
          behavior: "smooth",
        });
      }
    }
  }, [container, bubbleItems, thinkingChain]);

  return (
    <div className={styles.layout} ref={setContainer}>
      <div id="chatbi-messages" className={styles.chat}>
        <div className={styles.messages}>
          {/*  -  */}
          {bubbleItems.length === 0 && !loading && (
            <div className="cs-text-center cs-py-12">
              <div className="cs-mb-4 cs-text-gray-400">
                <LineChartOutlined style={{ fontSize: "48px" }} />
              </div>
              <h3 className="cs-text-xl cs-font-medium cs-mb-2">
                 Chat BI S
              </h3>
              <p className="cs-text-gray-500 cs-max-w-lg cs-mx-auto">
                
              </p>
            </div>
          )}

          {/*  */}
          {bubbleItems.length > 0 && (
            <Bubble.List
              autoScroll={true}
              items={bubbleItems}
              roles={roles}
              className={styles.messages}
            />
          )}

          {/*  -  */}
          <ThinkingChain
            steps={thinkingChain}
            isVisible={isGeneratingThinking}
            stage={processingStage}
          />

          {/*  */}
          <ProcessingProgress stage={processingStage} />

          {/*  */}
          <div ref={messagesEndRef} />
        </div>
      </div>

      <Affix offsetBottom={10} target={() => container} style={{ flex: 1 }}>
        <div className="cs-p-2 cs-flex cs-flex-col cs-gap-2 cs-bg-white">
          {/*  */}
          <Prompts
            items={senderPromptsItems}
            onItemClick={onPromptsItemClick}
          />

          {/*  */}
          <div className="cs-flex cs-justify-between cs-items-center">
            <Button
              onClick={clearMessages}
              size="small"
              danger={bubbleItems.length > 0}
            >
              
            </Button>

            {attachedFiles.length > 0 && (
              <div className="cs-text-sm cs-text-gray-500">
                 {attachedFiles.length} 
              </div>
            )}
          </div>

          {/*  */}
          <Sender
            value={prompt}
            header={senderHeader}
            onSubmit={handleSubmit}
            onChange={setPrompt}
            prefix={attachmentsNode}
            loading={loading || isGeneratingThinking}
            className={styles.sender}
            placeholder={
              loading || isGeneratingThinking
                ? "AI..."
                : "..."
            }
          />
        </div>
      </Affix>
    </div>
  );
};

export default Playground;
