import { useEffect } from "react";

/**
 *  requestIdleCallback API  hooks
 *  requestIdleCallback APIhttps://bugs.webkit.org/show_bug.cgi?id=164193
 * @param callback 
 * @param deps 
 */
export function useIdleCallback<T>(callback: unknown, deps: T[] = []) {
  useEffect(() => {
    const requestIdle =
      window.requestIdleCallback ||
      function (cb) {
        return setTimeout(() => {
          const start = Date.now();
          cb({
            didTimeout: false,
            timeRemaining: () => Math.max(0, 50 - (Date.now() - start)),
          });
        }, 1000);
      };

    const handle = requestIdle(callback as IdleRequestCallback);

    return () => {
      if (window.cancelIdleCallback) {
        window.cancelIdleCallback(handle);
      } else {
        clearTimeout(handle);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, deps);
}
