/**
 * Dify
 * TypeScriptDifyAPI
 */

// 
export interface WorkflowConfig {
  apiKey: string;
  inputs?: Record<string, any>;
  responseMode?: "streaming" | "blocking";
  user?: string;
  baseUrl?: string;
  timeout?: number;
}

// 
export interface StreamingOptions {
  onChunk?: (chunk: string) => void;
  onComplete?: () => void;
  onError?: (error: Error) => void;
}

// 
export interface RetryConfig {
  maxRetries?: number;
  retryDelay?: number;
  exponentialBackoff?: boolean;
}

// 
export interface BatchResult {
  successfulResults: Array<{
    config: WorkflowConfig;
    response: Response;
  }>;
  failedResults: Array<{
    config: WorkflowConfig;
    error: Error;
  }>;
  allSucceeded: boolean;
}

// API
export interface ApiErrorResponse {
  detail?: string;
  message?: string;
  error?: string;
  [key: string]: any;
}

/**
 * 
 */
export class WorkflowClient {
  private readonly defaultConfig: Required<
    Pick<WorkflowConfig, "baseUrl" | "responseMode" | "user" | "timeout">
  >;

  /**
   * 
   * @param {Partial<WorkflowConfig>} defaultConfig - 
   */
  constructor(defaultConfig: Partial<WorkflowConfig> = {}) {
    this.defaultConfig = {
      baseUrl: defaultConfig.baseUrl || DIFY_API_URL,
      responseMode: defaultConfig.responseMode || "streaming",
      user: defaultConfig.user || "abc-123",
      timeout: defaultConfig.timeout || 30000,
    };
  }

  /**
   * 
   * @param {WorkflowConfig} config - 
   * @returns {Promise<Response>} - fetch
   */
  async runWorkflow(config: WorkflowConfig): Promise<Response> {
    const mergedConfig = {
      ...this.defaultConfig,
      ...config,
    };

    // 
    if (!mergedConfig.apiKey) {
      throw new Error("API");
    }

    const url = `${mergedConfig.baseUrl}/v1/workflows/run`;
    const headers: HeadersInit = {
      Authorization: `Bearer ${mergedConfig.apiKey}`,
      "Content-Type": "application/json",
    };

    const body: Record<string, any> = {
      inputs: mergedConfig.inputs || {},
      response_mode: mergedConfig.responseMode,
      user: mergedConfig.user,
    };

    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(
        () => controller.abort(),
        mergedConfig.timeout
      );

      const response = await fetch(url, {
        method: "POST",
        headers,
        body: JSON.stringify(body),
        signal: controller.signal,
        keepalive: mergedConfig.responseMode === "streaming",
      });

      clearTimeout(timeoutId);

      // HTTP
      if (!response.ok) {
        const errorData = await this.parseErrorResponse(response);
        throw new Error(
          errorData.detail ||
            errorData.message ||
            errorData.error ||
            `: ${response.status} ${response.statusText}`
        );
      }

      return response;
    } catch (error) {
      if (error instanceof Error) {
        if (error.name === "AbortError") {
          throw new Error(`${mergedConfig.timeout}ms`);
        }
        throw error;
      }
      throw new Error(`: ${String(error)}`);
    }
  }

  /**
   * 
   * @param {Response} response - 
   * @returns {Promise<ApiErrorResponse>} - 
   */
  private async parseErrorResponse(
    response: Response
  ): Promise<ApiErrorResponse> {
    try {
      return await response.json();
    } catch {
      return { detail: await response.text() };
    }
  }

  /**
   * 
   * @param {Response} response - fetch
   * @param {StreamingOptions} options - 
   * @returns {Promise<void>}
   */
  async handleStreamingResponse(
    response: Response,
    options: StreamingOptions = {}
  ): Promise<void> {
    const {
      onChunk = (chunk) => console.log(":", chunk),
      onComplete = () => console.log(""),
      onError = (error) => console.error(":", error),
    } = options;

    try {
      if (!response.body) {
        throw new Error("");
      }

      const reader = response.body.getReader();
      const decoder = new TextDecoder();

      while (true) {
        const { done, value } = await reader.read();

        if (done) {
          onComplete();
          break;
        }

        if (value) {
          const chunk = decoder.decode(value, { stream: true });
          onChunk(chunk);
        }
      }
    } catch (error) {
      onError(error instanceof Error ? error : new Error(String(error)));
    }
  }

  /**
   * JSON
   * @param {Response} response - fetch
   * @returns {Promise<T>} - JSON
   */
  async handleJsonResponse<T = any>(response: Response): Promise<T> {
    return await response.json();
  }

  /**
   * 
   * @param {WorkflowConfig[]} configs - 
   * @returns {Promise<BatchResult>} - 
   */
  async runWorkflowsInParallel(
    configs: WorkflowConfig[]
  ): Promise<BatchResult> {
    if (configs.length === 0) {
      return {
        successfulResults: [],
        failedResults: [],
        allSucceeded: true,
      };
    }

    const promises = configs.map((config) =>
      this.runWorkflow(config).then((response) => ({ config, response }))
    );

    const results = await Promise.allSettled(promises);

    const successfulResults: BatchResult["successfulResults"] = [];
    const failedResults: BatchResult["failedResults"] = [];

    results.forEach((result, index) => {
      if (result.status === "fulfilled") {
        successfulResults.push(result.value);
      } else {
        failedResults.push({
          config: configs[index],
          error:
            result.reason instanceof Error
              ? result.reason
              : new Error(String(result.reason)),
        });
      }
    });

    return {
      successfulResults,
      failedResults,
      allSucceeded: failedResults.length === 0,
    };
  }

  /**
   * 
   * @param {WorkflowConfig} config - 
   * @param {RetryConfig} retryConfig - 
   * @returns {Promise<Response>} - fetch
   */
  async runWorkflowWithRetry(
    config: WorkflowConfig,
    retryConfig: RetryConfig = {}
  ): Promise<Response> {
    const {
      maxRetries = 3,
      retryDelay = 1000,
      exponentialBackoff = true,
    } = retryConfig;

    let lastError: Error | null = null;

    for (let attempt = 0; attempt < maxRetries; attempt++) {
      try {
        return await this.runWorkflow(config);
      } catch (error) {
        lastError = error instanceof Error ? error : new Error(String(error));

        // 
        if (attempt < maxRetries - 1) {
          const delay = exponentialBackoff
            ? retryDelay * Math.pow(2, attempt)
            : retryDelay;

          console.warn(` ${attempt + 1} ${delay}ms ...`);
          await new Promise((resolve) => setTimeout(resolve, delay));
        }
      }
    }

    if (lastError) {
      throw lastError;
    }
    throw new Error("");
  }

  /**
   * 
   * @returns {AbortController} - 
   */
  createAbortController(): AbortController {
    return new AbortController();
  }
}

// 
export const workflowClient = new WorkflowClient({
  timeout: 60000,
});

// 
export const queryCoreData = async (query: string) => {
  const jsonResponse = await workflowClient.runWorkflow({
    apiKey: DIFY_API_KEY,
    inputs: {
      query,
    },
    responseMode: "blocking",
    user: "user-001",
  });

  console.debug("queryCoreData: ", jsonResponse);
  return jsonResponse;
};

// 
export async function workflowExample(): Promise<void> {
  const client = new WorkflowClient({
    baseUrl: DIFY_API_URL,
    timeout: 60000,
  });

  try {
    // 1. 
    console.log("...");
    const streamResponse = await client.runWorkflow({
      apiKey: "your-api-key-here",
      inputs: {
        /*  */
      },
      responseMode: "streaming",
      user: "user-001",
    });

    await client.handleStreamingResponse(streamResponse, {
      onChunk: (chunk) => {
        console.log(":", chunk);
        // 
      },
      onComplete: () => {
        console.log("");
      },
    });

    // 2. JSON
    console.log("\nJSON...");
    const jsonResponse = await client.runWorkflow({
      apiKey: "your-api-key-here",
      inputs: {
        /*  */
      },
      responseMode: "blocking",
      user: "user-001",
    });

    const result = await client.handleJsonResponse(jsonResponse);
    console.log("JSON:", result);

    // 3. 
    console.log("\n...");
    const batchResult = await client.runWorkflowsInParallel([
      {
        apiKey: "your-api-key-here",
        inputs: { workflow: "workflow-1" },
        user: "user-001",
      },
      {
        apiKey: "your-api-key-here",
        inputs: { workflow: "workflow-2" },
        user: "user-002",
      },
    ]);

    console.log(":", batchResult.successfulResults.length);
    console.log(":", batchResult.failedResults.length);

    // 4. 
    console.log("\n...");
    const retryResponse = await client.runWorkflowWithRetry(
      {
        apiKey: "your-api-key-here",
        inputs: {
          /*  */
        },
      },
      {
        maxRetries: 5,
        retryDelay: 2000,
        exponentialBackoff: true,
      }
    );

    const retryResult = await client.handleJsonResponse(retryResponse);
    console.log(":", retryResult);
  } catch (error) {
    console.error(":", error);
  }
}

// 
if (require.main === module) {
  workflowExample().catch(console.error);
}
